import React from 'react';
import { Card, CardBody, Table } from 'reactstrap';
import { useQuery } from '@tanstack/react-query';
import useAxios from '../../hooks/useAxios';

const Spda = () => {
  const api = useAxios();

  const { isLoading, error, data } = useQuery({
    queryKey: ['spda'],
    queryFn: () =>
      api.get(`vapi/perusahaan/spda`).then((res) => {
        return res.data.data;
      }),
  });

  console.log(data);

  return (
    <Card className="rounded-4">
      <CardBody>
        {isLoading ? (
          'Loading..'
        ) : error ? (
          'Something went wrong.'
        ) : (
          <Table>
            <>
              <thead>
                <tr>
                  <th>No</th>
                  <th>Perusahaan</th>
                  <th>KKKS</th>
                  <th>No SPDA</th>
                  <th>Mulai Berlaku</th>
                  <th>Berakhir</th>
                  <th>Dokumen SPDA</th>
                  <th>Metode Upload</th>
                  <th>Status</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td></td>
                  <td>{data?.nama_perusahaan}</td>
                  <td></td>
                  <td></td>
                  <td></td>
                  <td></td>
                  <td></td>
                  <td></td>
                  <td></td>
                </tr>
              </tbody>
            </>
          </Table>
        )}
      </CardBody>
    </Card>
  );
};

export default Spda;
